
@mixin text($size, $l-height: null, $weight: null, $colour: null){
	font-size: $size;
	line-height: $l-height;
	font-weight: $weight;
	color: $colour;
}

@mixin phone{
 @media(max-width: #{$phone - 1px}){
	 @content;
 				}
		}

	
	@mixin large-phone{
	 @media(min-width: #{$phone}) and (max-width: #{$tablet - 1px}){
		 @content;
	 				}
			}


	@mixin tablet{
			@media(min-width: #{$tablet}) and (max-width: #{$desktop - 1px}){
			@content;
					}
				   }		
			

	@mixin desktop{
	 @media(min-width: #{$desktop}){
			 @content;
		 				}
			}
